<template>
  <!-- 注册页面的整体盒子 -->
  <div class="reg-container">
    <!-- 注册的盒子 -->
    <div class="reg-box">
      <!-- 标题的盒子 -->
      <div class="title-box"></div>
      <!-- 注册的表单区域 -->
      <el-form :model="regForm" :rules="rulesObj" ref="regRef">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input
            v-model="regForm.username"
            placeholder="请输入用户名"
          ></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input
            v-model="regForm.password"
            type="password"
            placeholder="请输入密码"
            show-password
          ></el-input>
        </el-form-item>
        <!-- 确认密码 -->
        <el-form-item prop="repassword">
          <el-input
            v-model="regForm.repassword"
            type="password"
            placeholder="请再次确认密码"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="btn-reg" @click="registerFn"
            >注册</el-button
          >
          <el-link type="info" @click="$router.push('/login')">去登录</el-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { registerAPI } from '@/api/index'
// 经验
// 前端绑定数据对象属性名，可以直接给要调用的功能接口的参数名一致
// 好处：我可以直接把前端对象（带着同名的属性和前端的值）发给后台

// 需求四步
// 第一步：标签样式
// 第二步：数据绑定、数据铺设
// 第三步：JS交互
// 第四部：是否发送给后台
export default {
  name: 'my-register',
  data () {
    // 简单记忆 data函数return一个对象
    // 确认密码的逻辑
    const samePwdFn = (rule, value, callback) => {
      // 因为要通过this.来拿到输入的密码，所以要写在data里
      if (value !== this.regForm.password) {
        // 如果验证失败，则调用回调函数，指定一个error对象
        callback(new Error('两次输入的密码不一致'))
      } else {
        // 如果验证成功，则直接调用callback回调函数即可
        callback()
      }
    }
    return {
      regForm: {
        // 表单数据对象
        username: '', // 用户名
        password: '', // 密码
        repassword: '' // 确认密码
      },
      rulesObj: {
        // 表单规则对象
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          {
            pattern: /^[a-zA-Z0-9]{1,10}$/,
            message: '长度在 1 到 10 个字符',
            trigger: 'blur' // blur：表单失去焦点
          } // 正则验证用户名是否合法
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            pattern: /^\S{6,15}$/,
            message: '长度在 6 到 15 个字符',
            trigger: 'blur'
          }
        ],
        repassword: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          { validator: samePwdFn, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 注册点击事件
    registerFn () {
      // JS兜底校验
      this.$refs.regRef.validate(async (boolean) => {
        if (boolean) {
          // 通过校验
          // console.log(this.regForm)
          const { data: res } = await registerAPI(this.regForm)
          //   return很重要，阻止代码继续往下执行
          if (res.code !== 0) return this.$message.error(res.message)
          this.$message.success(res.message)
          //   路由跳转到登录页面
          this.$router.push({
            path: '/login'
          })
        } else {
          // 未通过校验
          return false // 阻止默认提交行为(表单下面红色提示会自动出现),防止用户直接点注册，输入框没有失焦
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.reg-container {
  background: url("../../assets/images/login_bg.jpg") center;
  background-size: cover;
  height: 100%;

  .reg-box {
    width: 400px;
    height: 335px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 0 30px;
    box-sizing: border-box;

    .title-box {
      height: 60px;
      background: url("../../assets/images/login_title.png") center no-repeat;
    }

    // .el-input {
    //   margin-top: 18px;
    // }
    .btn-reg {
      width: 100%;
    }
    // .el-link {
    //   margin-top: 15px;
    //   font-size: 15px;
    // }
  }
}
</style>
